import uniIndexedList from "@/components/uni-indexed-list/uni-indexed-list.vue"
export default {
	data() {
		return {
			cityName: '莆田市',
			factoryList: [{
				"letter": "c",
				"data": [
					"测试自提"
				]
			}, {
				"letter": "p",
				"data": [
					"莆田工信鞋业"
				]
			}],
		}
	},
	components: {
		uniIndexedList,
	},
	onLoad(options) {
		this.cityName = options.cityName || '莆田市';
		this.getFactorys();
	},
	created() {

	},
	methods: {
		// 请求工厂
		getFactorys(){
			uni.request({
				url: 'https://api-t.st-llshop.surex.cc/v1/pickup/pickuplistr',
				method: 'post',
				data: {
					warehouse_id: 7
				},
				success: res => {
					console.log("res:", res);
				},
				fail: (err) => {
					
				}
			});
		},
		bindClick(e) {
			// 选择工厂
			let factoryName = e.item.name;
			uni.switchTab({
				url: `../../tabBar/index/index`
			});
			uni.$emit('factoryName',{msg:`${factoryName}`})
		}
	}
}
